import { Component, OnInit } from '@angular/core';
import { MarkSixResult } from '../models/common.model';
import { CommonService } from '../services/common.service';
import { Mark6Service } from '../services/mark6.service';
import { Router } from '@angular/router';
import { NavController, LoadingController } from '@ionic/angular';

@Component({
  selector: 'app-topnum',
  templateUrl: './topnum.component.html',
  styleUrls: ['./topnum.component.scss']
})
export class TopNumComponent implements OnInit {

  public loaded = false;
  public m6results: MarkSixResult[];
  public topnums: Number[][];

  constructor(private commonService: CommonService, private m6service: Mark6Service, private _router: Router,
    private navCtrl: NavController, private loadingController: LoadingController) { }

  ngOnInit() {
    this.doRefresh();
  }

  async doRefresh() {
    this.loaded = false;

    let loading = await this.loadingController.create({
      message: 'Loading',
      spinner: 'circles'
    });

    loading.present().then(() => {
      this.m6service.getLast30().subscribe(result => {
        this.m6results = result;
        loading.dismiss();
        this.topnums = this.m6service.getTopNumbers(this.m6results);

        this.loaded = true;
      }, error => {
        loading.dismiss();
        this.loaded = false;
        console.log(error);
      });
    });
  }
}
